@import '../../abstracts/mixins';



@include respond(tab-port){

$EduExp__CRD__color:  rgb(41, 41, 41);
$border-radius: 0px;
$cardhight:20px;
$time-toPopupCrd: .9s ease;
.CardEdu{
    margin:2.2rem 1rem;
    background: transparent;
    border-top-width: 1px;
    min-height: $cardhight;
    min-width: 220px;
    max-width: 280px;
    color: $EduExp__CRD__color;
    box-shadow: none ;
    border-radius:$border-radius;
    border-radius: 11px ;
    overflow: hidden;
    &_IMG_Con{
        height: 71px;
        object-fit: contain;
        box-shadow: none ;
        filter: grayscale(1);
        border-radius: 11px ;
        border-bottom-width: 1px;
        box-shadow: 0 -9px 44px black;
           // // gardient cover on top of the imag <<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<
            &:after {
                position: absolute;
                content:"";
                height:100%;
                width:100%;
                top:0;
                left:0;
                background: linear-gradient(to right, rgba(9, 70, 128, .7) 7%,rgba(255, 255, 255, .8)); 
            }
            & img{
                width: 120%;
                height: 160%;
                // object-position: center bottom;
                position: relative;
                object-fit:cover;
                transition: all $time-toPopupCrd;

            }
        transition: all $time-toPopupCrd;
    }

    &_INFO_Con{
        padding:1.7rem .5rem;
        .CardEdu_header .CardEdu_header_main_title:after{
            content: attr(extrainfo);//'19 - Present';/
            font-family: 'Lato', sans-serif;
            position: absolute;
            background-color: transparent;
            font-size: 1.1rem;
            white-space: nowrap;
            top: -50%;
            opacity: .4;
            right: 0;
            font-weight: 600;
            border: none;
            transition: all $time-toPopupCrd;
        }
        
        .CardEdu_header{
            border-bottom: 1px solid rgba(196, 196, 196, 0.877);

            &_icon{
                fill: lighten($EduExp__CRD__color, 40% )   ;
                transition: all $time-toPopupCrd;
            }
            &_main_title{
                font: 1.3rem 'Lato', sans-serif;
                letter-spacing: .1ch;
                font-weight: 400;
                position: relative;
                &::after{
                    color:blue;
                }
        } } }
        &_second_title{
            margin-top: .2rem;
            font-size: 1.8rem;
            letter-spacing: .3ch;
        }
        &_paragraph{
            line-height: 1.9rem ;
            font-weight: 300;
            letter-spacing: .1ch;
        }
    
    transition: transform $time-toPopupCrd , box-shadow $time-toPopupCrd;

    &:hover{
        transform: translateY(0) scale(1);
        box-shadow: none;

        .CardEdu_IMG_Con:hover{
            height: 188px;
            img{
                // transition: all 34s linear ;
                // animation: picture_bg_Swimming 23s infinite alternate linear;
                animation: none;
            }
        }
       
        .CardEdu_header {
            &_icon{
                fill:rgb(99, 185, 255);
                transition: all $time-toPopupCrd;
            }
            .CardEdu_header_main_title:after{
                opacity: .7;
                transition: all $time-toPopupCrd;
            }

        }
        transition: transform $time-toPopupCrd , box-shadow $time-toPopupCrd;
    }
    

    // transform is the transition for expanding the image

   
   


}


}